import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import Fade from "@material-ui/core/Fade";
import Paper from "@material-ui/core/Paper";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { Route, NavLink, HashRouter } from "react-router-dom";

const styles = {
  button: {
    "font-size": "calc(0.8em + 0.8vw)"
  },
  inlineButton: {
    margin: "5px 10px"
  },
  AboutDiv: {
    //   "background-color": "#03a9f4",
  }
};
class About extends Component {
  render() {
    return (
        <Fade in={true} timeout={500}>
        <div className={this.props.classes.AboutDiv}>
          <Paper elevation={4} style={{margin: "5px 5%", padding: "5px"}}>
          <div className="App-intro">
            Hey there! My name's Alex (in case nothing else gave it away), I'm
            currently an undergrad at
            <Button
              variant="outlined"
              color="primary"
              className={this.props.classes.inlineButton}
            >
              <a
                className="navlinks small-link gatech"
                target="_blank"
                href="https://gatech.edu"
              >
                Georgia Tech
              </a>{" "}
            </Button>{" "}
            studying Computer Science and Linguistics!
            <Divider />
            Please 👏 Hire 👏 Me 👏
            <Divider />
            Will code for money... or debug code for money, or talk about code,
            or literally anything I'll dig up the grave of Ada Lovelace for you
            just dear god I'm so poor please give me money
            <Divider />
          </div>

          <Button
            variant="outlined"
            color="secondary"
            className={this.props.classes.inlineButton}
          >
            <a
              className="navlinks small-link"
              target="_blank"
              href="https://gatech.edu"
            >
              My CV
            </a>{" "}
          </Button>
          <Button
            variant="outlined"
            color="primary"
            className={this.props.classes.button}
          >
            <NavLink className="navlinks" to="/">
              Going back? So soon?
            </NavLink>
          </Button>
          </Paper>
        </div>
      </Fade>
    );
  }
}

About.propTypes = {
  children: PropTypes.node,
  classes: PropTypes.object.isRequired
};
export default withStyles(styles)(About);



