import React, { Component } from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Particles from "react-particles-js";
import Avatar from "@material-ui/core/Avatar";
import Fade from "@material-ui/core/Fade";
import Paper from "@material-ui/core/Paper";
import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile
} from "react-device-detect";

import "./App.css";

import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";

import { Route, NavLink, BrowserRouter, Switch } from "react-router-dom";
// import { TransitionGroup, CSSTransition } from "react-transition-group";
import { AnimatedSwitch } from "react-router-transition";

import Intro from "./Intro";
import About from "./About";

const particle_colour = "#e91e63";

const num_particles = isMobile ? 20 : 80;

const particle_config = {
  particles: {
    number: {
      value: num_particles,
      density: {
        enable: true,
        value_area: 800
      }
    },
    color: {
      value: particle_colour
    },
    shape: {
      type: "circle",
      stroke: {
        width: 0,
        color: "#000000"
      },
      polygon: {
        nb_sides: 5
      }
    },
    opacity: {
      value: 0.5,
      random: false,
      anim: {
        enable: false,
        speed: 1,
        opacity_min: 0.1,
        sync: false
      }
    },
    size: {
      value: 4,
      random: true,
      anim: {
        enable: false,
        speed: 40,
        size_min: 0.1,
        sync: false
      }
    },
    line_linked: {
      enable: true,
      distance: 150,
      color: particle_colour,
      opacity: 0.4,
      width: 2
    },
    move: {
      enable: true,
      speed: 3,
      direction: "none",
      random: false,
      straight: false,
      out_mode: "out",
      bounce: false,
      attract: {
        enable: false,
        rotateX: 600,
        rotateY: 1200
      }
    }
  },
  interactivity: {
    detect_on: "canvas",
    events: {
      onhover: {
        enable: false,
        mode: "repulse"
      },
      onclick: {
        enable: false,
        mode: "push"
      },
      resize: true
    },
    modes: {
      grab: {
        distance: 400,
        line_linked: {
          opacity: 1
        }
      },
      bubble: {
        distance: 400,
        size: 40,
        duration: 2,
        opacity: 8,
        speed: 3
      },
      repulse: {
        distance: 50,
        duration: 0.1
      },
      push: {
        particles_nb: 4
      },
      remove: {
        particles_nb: 2
      }
    }
  },
  retina_detect: true
};

const av_dim = isMobile ? 100 : 150;
const title_size = isMobile ? 3 : 4;
const right_margin = isMobile ? 0 : 20;
const styles = {
  bigAvatar: {
    height: av_dim,
    width: av_dim,
    top: 30,
    bottom: 50,
    "margin-right": right_margin,
    "z-index": 0
  },
  appTitle: {
    color: "white",
    "font-size": "calc(" + title_size + "em + " + title_size + "vw)"
  }
};

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div className="App">
          <Particles params={particle_config} className="particleCanvas" />
          <CssBaseline />
          <header className="App-header">
            <Avatar
              alt="Me!"
              src="/static/images/me_glass.jpg"
              className={this.props.classes.bigAvatar}
            />
            <h1 className={this.props.classes.appTitle}>Alex Gurung</h1>
          </header>
          <div className="content">
            {/* <Fade in={true}> */}
              {/* <Paper elevation={3} style={{ margin: "5px 5%", padding: "5px", "background-color": "white" }}> */}
                <Switch>
                  <Route exact path="/" component={Intro} />
                  <Route exact path="/About" component={About} />
                  <Route render={() => <div>Not Found</div>} />
                </Switch>
              {/* </Paper> */}
            {/* </Fade> */}
          </div>
        </div>
      </BrowserRouter>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(App);



